.subtitle{margin-top: 10px;
display: flex;
justify-content: space-evenly;
gap: 30%;
height: 4rem;
}
.subtitle .inp1{
    width: 13rem;
    color: var(--VeryDarkBlueLightText);
    background-color: var(--DarkGrayLightInput);
    height: 2rem;
}
.subtitle select{
    width: 7rem;
    text-transform: capitalize;
    background-color: var(--DarkGrayLightInput);
    color: var(--VeryDarkBlueLightText);
    border-radius: 10px;


    height: 2rem;
}
.subtitle select option{
    border-radius: 20px;
    background-color: var(--DarkGrayLightInput);
        border-radius: 10px;

    color: var(--VeryDarkBlueLightText);

}

.cards{ 
    display:flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 15rem;
    height: 34rem;
    display: flex;
}
.card{
    height: 20rem;
    text-transform: capitalize;
    font-size: 24px;
    color: var(--VeryDarkBlueLightText);
    background-color: var(--DarkGrayLightInput);
    border-radius: 5px;
    transition: 0.5s;
}
.card:hover{
    scale: 1.2;
    cursor: pointer;
    transform: rotate(5deg);

}
.card img{
    border-radius: 5px;
}
button{
    width: 5rem;
    height: 2rem;
    border-radius: 10px;
    color: var(--VeryDarkBlueLightText);
    background-color: var(--DarkGrayLightInput);
}
