
.main{
    padding: 5px;
    display: flex;
    justify-content: space-between;
    
    border: 1px solid;
    /* background-color: var(--DarkGrayLightInput); */
    border-radius: 10px;
    box-shadow: 3px 3px;
    height: 60px;
    width: 100%;
    /* border-bottom:2px solid var(--DarkGrayLightInput) ; */
}
.main h1{
   color: var(--VeryDarkBlueLightText);
   text-transform: capitalize;
}
.darkMode h1{
    font-size: 17px;
    color: var(--VeryDarkBlueLightText);
    text-transform: capitalize;

}
.darkMode{
    margin-top: 11px;
    padding-top:4px ;
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    gap: 10px;
    border-radius: 10px;
        /* height: 40px;
        width: 120px; */
        transition: 0.5s;
}
.darkMode i{
    margin-top: 12px;
}

.darkMode:hover{
    background-color: var(--DarkGrayLightInput);
    cursor: pointer;
}

@media screen and (max-width:500px) {

     h1{
        font-size: 20px;
    }
    .darkMode{
        margin-top: 1px;
       
    }

    .main{
      
        padding-top: 1%;

       
        height: 50px;
       
    }
}
    
